.Skills {
    background-color: #141313;
    padding-bottom: 5%;
}

.Skills p {
    padding-left: 17%;
    width: 85%;
}

.Skills .List ul {
    color: white;
    list-style-type: none;
    padding: 0;
}

.Skills .Container {
    display: flex;
    flex-wrap: wrap; 
    margin-left: 10%;
    margin-right: 10%;
}

.Skills .Container .Image {
    width: 100%;
    padding-left: 20%;
    padding-bottom: 1%;
    justify-content: center;
}

.Skills .Container .List {
    flex-basis: 33%;
    background-color: black;
    box-shadow: 0 0 12px white;
    border-radius: 9px;
    margin-bottom: 2px;

}

.Skills .List ul li {
    display: flex;
    justify-content: center;
    font-size: 18px;
}